.place {
  &__photo-container {
    position: relative;

    box-sizing: border-box;
    width: 100%;
    max-height: 256px;
    overflow: hidden;
    border-radius: 8px;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    

    object-fit: cover;
    object-position: center;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      
    }
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop{
      margin-bottom: 24px;
    }
  }

  &__link {
    font-weight: bold;
    color: $colorBlue;
    text-decoration: none;
    transition: 0.3s;

    &:hover {
      color: #000;
    }
  }
}